/* @flow */
import Arrow, { DoubleArrow } from 'src/components/styled/Arrow';
import COLORS from 'src/styles/COLORS';
import OverflowWrapper from 'src/components/tables/OverflowWrapper';
import React from 'react';
import styled, { css } from 'react-emotion';

export type Props = {
  data: Array<Object>,
  cols: Array<Object>,
};

const clickableHeading = css`
  cursor: pointer;
  transition: color 150ms;
  user-select: none;

  :hover {
    color: ${COLORS.LINK};
  }

  :hover .arrow {
    border-top-color: ${COLORS.LINK};
  }
`;

const genericCell = css`
  height: 50px;
  vertical-align: middle;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 15px;
  padding-right: 15px;

  @media (max-width: 991px) {
    padding-left: 7px;
    padding-right: 7px;
  }
`;

const BodyCell = styled.td`${genericCell};`;

const HeadingCell = styled.td`
  ${genericCell};
  width: ${props => (props.isMain ? '100%' : 'auto')}
  font-weight: 700;
  white-space: nowrap;
`;

const HeadingCellClickableArea = styled.div`
  ${props => (props.isClickable ? clickableHeading : '')};
  color: ${props => (props.isSorting ? COLORS.LINK : 'inherit')};
  display: inline-flex;
  align-items: center;
  height: 100%;
  width: 100%;
  text-align: inherit;

  .arrow {
    transform: ${props =>
      props.isSorting && props.sortOrder === 'asc' ? 'rotate(180deg)' : 'none'};
    border-top-color: ${props =>
      props.isSorting ? COLORS.LINK : COLORS.FOREGROUND};
  }
`;

const Table = styled.table`width: 100%;`;

const rowStyles = css`border-top: 1px solid ${COLORS.BORDER_GRAY};`;
const HeadingRow = styled.tr(rowStyles);
const ContentRow = styled.tr`
  ${rowStyles};
  transition: background-color 150ms;

  &:hover {
    background-color: rgba(0, 0, 0, 0.75);
  }
`;

export class DataTable extends React.Component {
  props: Props;

  renderBody = (row, col) => {
    const { render, component, ...colPassThrough } = col;

    const data = {
      col: colPassThrough,
      ...row,
    };

    if (render) {
      return col.render(data);
    }

    if (component) {
      return React.createElement(col.component, data);
    }

    return row[col.key];
  };

  handleHeadingClick = col =>
    this.props.onHeadingClick && this.props.onHeadingClick(col.key);

  render() {
    const {
      data,
      cols,
      renderEmpty,
      mainCol,
      sortKey,
      sortOrder,
      contentRowClass,
    } = this.props;
    return (
      <OverflowWrapper minWidth={this.props.minWidth}>
        <Table>
          <thead>
            <HeadingRow>
              {cols.map(col => (
                <HeadingCell
                  isMain={col.key === mainCol}
                  key={col.name}
                  className={col.className}
                >
                  <HeadingCellClickableArea
                    isSorting={col.key === sortKey}
                    sortOrder={sortOrder}
                    onClick={() => this.handleHeadingClick(col)}
                    isClickable={col.isSortable}
                    className={col.headingClass}
                  >
                    {col.name}
                    {col.isSortable &&
                      (col.key === this.props.sortKey ? (
                        <Arrow className="arrow" css={`margin-left: 5px;`} />
                      ) : (
                        <DoubleArrow>
                          <Arrow className="arrow" css={`margin-left: 5px;`} />
                          <Arrow className="arrow" css={`margin-left: 5px;`} />
                        </DoubleArrow>
                      ))}
                  </HeadingCellClickableArea>
                </HeadingCell>
              ))}
            </HeadingRow>
          </thead>

          <tbody>
            {data.map(row => (
              <ContentRow
                key={row.id}
                className={
                  typeof contentRowClass === 'function'
                    ? contentRowClass(row)
                    : contentRowClass
                }
              >
                {cols.map(col => (
                  <BodyCell
                    key={`${row.id}-${col.key}`}
                    className={col.className}
                  >
                    {this.renderBody(row, col)}
                  </BodyCell>
                ))}
              </ContentRow>
            ))}
          </tbody>

          {data.length === 0 &&
            renderEmpty && (
              <tfoot>
                <tr>
                  <td colSpan={cols.length}>{this.props.renderEmpty()}</td>
                </tr>
              </tfoot>
            )}
        </Table>
      </OverflowWrapper>
    );
  }
}

export default DataTable;



